@import url('https://fonts.googleapis.com/css?family=Roboto');

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  color: #333;
}
p {
  font-size: 15px;
    line-height: 1.8;
    color: #6d6d6d;
}
.content {
  max-width: 1440px;
  margin: 0 20px;
  padding: 60px 10px;
  display: grid;
  grid-gap: 20px;
}
.sidebar {
}
.main {
}
.short-description p {
  font-size: 17px;
}
.gravatarImage {
  border-radius: 50%;
}
.social-icons > a {
  color: inherit;
  margin-right: 5px;
}
.social-icons > a:hover {
  opacity: 0.8;
}
.main-image {
  width: 100%;
}

@media screen and (min-width: 576px) {
  .content {
    margin: 0 auto;
    padding: 60px;
    grid-template-columns: 1fr 3fr;
    grid-template-areas: "sidebar main";
    grid-gap: 60px;
  }
  .sidebar {
    grid-area: sidebar;
  }
  .main {
    grid-area: main;
  }
}



